.main-footer {
  background: #202020;
  padding: 35px 0 0;
  color: #959595;
  margin-top: 20px;
}

.main-footer .widget {
  //padding: 0 30px
}

.main-footer .widget .title {
  color: #fff;
  border-bottom: 1px solid #303030
}

.main-footer .widget .tag-cloud a {
  border: 1px solid #303030
}

.main-footer .widget .tag-cloud a:hover {
  border: 1px solid #337ab7
}


.widget {
  margin-bottom: 35px
}

.widget .title {
  margin-top: 0;
  padding-bottom: 7px;
  border-bottom: 1px solid #ebebeb;
  margin-bottom: 21px;
  position: relative
}

.widget .title:after {
  content: "";
  width: 90px;
  height: 1px;
  background: #337ab7;
  position: absolute;
  left: 0;
  bottom: -1px
}

.widget .friend-links a {
  display: block;
  border-bottom: 1px dashed #303030;
  padding-bottom: 14px;
  margin-bottom: 14px;
  color: #959595;
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease
}

.widget .friend-links a:hover {
  color: #fff;
  text-decoration: none
}

.widget .friend-links a:last-child {
  margin-bottom: 0
}

.widget .tag-cloud a {
  border: 1px solid #ebebeb;
  padding: 2px 7px;
  color: #959595;
  line-height: 1.5em;
  display: inline-block;
  margin: 0 7px 7px 0;
  -webkit-transition: all .2s ease;
  -o-transition: all .2s ease;
  transition: all .2s ease
}

.widget .tag-cloud a:hover {
  color: #fff;
  background: #337ab7;
  border: 1px solid #337ab7;
  text-decoration: none
}

.submit-site {
  background-color: #eee;
  padding-top: 40px;
  padding-bottom: 40px;
  margin-top: 20px
}


.copyright {
  background: #111;
  font-size: 13px;
  text-align: center;
  color: #555;
  padding-top: 14px;
  padding-bottom: 20px;
  border-top: 1px solid #303030
}

.copyright span {
  margin: 0 .5em
}

.copyright a {
  color: #555
}


